import { useSetRecoilState } from "recoil";
import { Categories, IToDo, ToDoState } from "../atoms";
import styled from "styled-components";
/* 
    1. find to do based on id [2] - target 
    2.  todo  update
    3. replace the to do in the index 
*/
const StyledList = styled.li`
    margin-top: 8px;
    div{
        background: rgba(51, 51, 51, 0.7);
        padding: 12px 16px;
        border-radius: 8px;
    }
`;
const ButtonContainer = styled.section`
    display: flex;
    justify-content: flex-end;
    button{
        margin-top: 8px;
        background: rgba(56, 139, 255, 0.2);
        color: #5a9eff;
        &:nth-child(2),&:nth-child(3){
            margin-left: 8px;
        }
        &:last-child{
            background: rgba(239, 84, 73, 0.2);
            color: #f34b3f;
        }
    }
`;
function ToDo({text, category, id}:IToDo){
    /* 1.  newCategory 
    const onClick = (newCategory:IToDo["category"])=>{ */
    /* 2. onClick newCategory  */
    const setToDos = useSetRecoilState(ToDoState);
    const onClick = (e:React.MouseEvent<HTMLButtonElement>)=>{
        const {currentTarget:{name}} = e;//e.currentTarget.name;
        setToDos(oldToDos => {
            const targetIndex = oldToDos.findIndex(toDo => toDo.id === id);// console.log(targetIndex);
            // const oldToDo = oldToDos[targetIndex];
            const newToDo = {text, id, category: name as IToDo["category"]};
            // console.log(oldToDo, newToDo);
            return [
                ...oldToDos.slice(0, targetIndex), 
                newToDo, 
                ...oldToDos.slice(targetIndex+1)];
                /*    
                    1. setTodos((oldToDos) => {
                    const targetIndex = oldToDos.findIndex((todo) => todo.id === id);
                    const newTodo: ITodo = { id, text, category: categoryState as ITodo["category"],};
                    const newTodos = [...oldToDos]; // create new array
                    newTodos.splice(targetIndex, 1, newTodo);
                    return newTodos;
                    });
                    2. setToDos(
                    (oldToDos) =>
                    oldToDos.map((oldToDo) => {
                    if (oldToDo.id === id) {
                    return { text, id, category: name as any };
                    }
                    return oldToDo;
                    }),
                    ) */
        }
        );
    }
    const deleteToDo = ()=>{
        setToDos((oldToDos)=>{
            const newToDo = oldToDos.filter((toDo)=>toDo.id !== id);
            return newToDo;
        })
    }
    return(
        <StyledList>
            <div>{text}</div>
            {/* and  :   false  false  */}
            {/* 1.  newCategory 
            {category !== "DOING" && <button onClick={()=> onClick("DOING")}> </button>}  */}
            {/* 2. onClick newCategory  */}
            <ButtonContainer>
            {category !== Categories.DOING && <button name={Categories.DOING} onClick={onClick}> </button>} 
            {category !== Categories.TODO && <button name={Categories.TODO} onClick={onClick}> </button>}
            {category !== Categories.DONE && <button name={Categories.DONE} onClick={onClick}></button>}
            <button onClick={deleteToDo}></button>
            </ButtonContainer>
        </StyledList>
    );
}

export default ToDo;